@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/Roboto-Condensed.woff2") format("woff2"), url("../fonts/Roboto-Condensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  overflow-x: hidden;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  display: block;
  color: #000000;
  text-decoration: none;
  user-select: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  a:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
a.focus-visible:focus {
  opacity: 0.5;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1,
h2,
h3,
h4,
p,
figure {
  margin: 0;
}

svg {
  display: block;
}

img,
video {
  display: block;
}

textarea {
  resize: none;
}

[data-validate=picture] {
  display: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input [type=email],
input [type=number],
input [type=password],
input [type=search],
input [type=tel],
input [type=text],
input [type=url] {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  user-select: none;
  cursor: pointer;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.wrapper__box {
  display: flex;
  justify-content: center;
}
@media (max-width: 1199px) {
  .wrapper__box {
    flex-direction: column;
    align-items: center;
  }
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  display: flex;
  padding: 18px 20px;
  width: 100%;
  color: #ffffff;
  background-color: transparent;
  border: 4px solid #ffffff;
  cursor: pointer;
  justify-content: center;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.4;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.btn.focus-visible:focus {
  opacity: 0.5;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  color: #ffffff;
  background-color: transparent;
  align-items: center;
}
@media (max-width: 1199px) {
  .header {
    margin-bottom: 20px;
  }
}

@media (max-width: 1199px) {
  .header img {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .header img {
    width: 69px;
    height: 24px;
  }
}

.header__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 160px;
  justify-content: end;
  align-self: flex-start;
}

.header__link {
  font-family: "Alumni Sans", "Tahoma", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  color: #ffffff;
  letter-spacing: 1px;
}
@media (max-width: 1199px) {
  .header__link {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.5px;
  }
}
@media (max-width: 767px) {
  .header__link {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

.entry {
  background: conic-gradient(from 135deg at 16% 40%, #003d85 9%, #b80027 87%, #003d85);
  padding-top: 30px;
  padding-bottom: 51px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .entry {
    padding-top: 35px;
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .entry {
    padding-top: 15px;
    padding-bottom: 80px;
  }
}

.entry__wrapper {
  display: flex;
}

.entry__wrapper img {
  margin-top: -25px;
}
@media (max-width: 1199px) {
  .entry__wrapper img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .entry__wrapper img {
    display: none;
  }
}

.entry__inner {
  align-self: flex-start;
  display: grid;
  grid-template-columns: repeat(2, auto);
  margin-right: 64px;
  margin-top: 88px;
  margin-left: 20px;
  column-gap: 17px;
}
@media (max-width: 1199px) {
  .entry__inner {
    margin-top: 47px;
    margin-right: 30px;
    margin-left: 0;
    width: 48%;
  }
}
@media (max-width: 767px) {
  .entry__inner {
    margin-top: 50px;
    margin-right: 0;
    width: 100%;
    column-gap: 0;
  }
}

.entry__inner span {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  display: block;
  grid-column: 1;
  grid-row: 2;
  margin-top: 120px;
  justify-self: start;
  text-align: right;
  max-width: 113px;
}
@media (max-width: 1199px) {
  .entry__inner span {
    margin-top: 90px;
  }
}
@media (max-width: 767px) {
  .entry__inner span {
    margin-top: 77px;
    margin-right: -15px;
  }
}

.entry__inner p {
  font-family: "Alumni Sans", "Tahoma", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  margin-bottom: 15px;
  margin-right: 50px;
  text-align: right;
  grid-column: 2;
  letter-spacing: 1px;
  max-width: 200px;
  justify-self: end;
}
@media (max-width: 1199px) {
  .entry__inner p {
    font-size: 34px;
    line-height: 41px;
    margin-bottom: 8px;
    margin-right: 5px;
  }
}
@media (max-width: 767px) {
  .entry__inner p {
    font-size: 28px;
    line-height: 34px;
    margin-bottom: 13px;
    margin-right: 3px;
  }
}

.entry__inner h1 {
  font-family: "Alumni Sans", "Tahoma", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-transform: uppercase;
  margin-bottom: 50px;
  text-align: right;
  grid-column: 2;
  letter-spacing: 5px;
  max-width: 250px;
  justify-self: end;
}
@media (max-width: 1199px) {
  .entry__inner h1 {
    font-weight: 700;
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
    margin-bottom: 38px;
    letter-spacing: 2.5px;
  }
}
@media (max-width: 767px) {
  .entry__inner h1 {
    margin-bottom: 25px;
  }
}

.entry__inner a {
  grid-column: 2;
  max-width: 235px;
  justify-self: end;
  letter-spacing: 1px;
}
@media (max-width: 1199px) {
  .entry__inner a {
    letter-spacing: 0.5px;
    max-width: 100%;
    padding: 18px 20px;
  }
}
@media (max-width: 767px) {
  .entry__inner a {
    grid-column: 1/-1;
  }
}

.gym {
