  font-size: 54px;
  line-height: 65px;
  margin-bottom: 38px;
  letter-spacing: 1.5px;
}
@media (max-width: 1199px) {
  .contacts__wrapper h2 {
    font-size: 42px;
    line-height: 50px;
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper h2 {
    font-size: 32px;
    line-height: 38px;
    margin-bottom: 35px;
    letter-spacing: 1px;
  }
}

.contacts__list {
  width: 100%;
  display: block;
  position: relative;
  column-count: 2;
  column-gap: 120px;
  margin-left: 3px;
}
.contacts__list::before {
  position: absolute;
  content: "";
  width: 1px;
  height: 100%;
  background-color: #74819c;
  right: 50%;
}
@media (max-width: 767px) {
  .contacts__list::before {
    display: none;
  }
}
@media (max-width: 1199px) {
  .contacts__list {
    margin-left: 0;
    column-gap: 90px;
  }
}
@media (max-width: 767px) {
  .contacts__list {
    column-count: auto;
  }
}

.contacts__item {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 15px;
  align-items: flex-start;
}
@media (max-width: 1199px) {
  .contacts__item {
    margin-bottom: 20px;
  }
}
.contacts__item:last-child {
  margin-bottom: 0;
}

.contacts__item h3 {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 5px;
}
@media (max-width: 1199px) {
  .contacts__item h3 {
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 5px;
  }
}

.contacts__item span {
  display: block;
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  break-before: avoid;
}
@media (max-width: 1199px) {
  .contacts__item span {
    font-size: 18px;
    line-height: 27px;
  }
}

.contacts__item a {
  color: #102654;
}

.footer {
  background-color: #030a1a;
  color: #ffffff;
  padding: 35px 0;
}
@media (max-width: 1199px) {
  .footer {
    padding: 40px 0;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 53px 0;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__wrapper img {
  margin-top: 5px;
}
@media (max-width: 1199px) {
  .footer__wrapper img {
    width: 82px;
    height: 18px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .footer__wrapper img {
    width: 68px;
    height: 15px;
    margin-bottom: -20px;
  }
}

@media (max-width: 767px) {
  .nav {
    margin-bottom: 25px;
  }
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 500px;
  margin-left: 8px;
}
@media (max-width: 1199px) {
  .nav__list {
    max-width: 420px;
    margin-left: 10px;
  }
}
@media (max-width: 767px) {
  .nav__list {
    display: block;
    column-count: 2;
    column-gap: 75px;
    margin-left: 0;
  }
}

.nav__item {
  margin-right: 40px;
}
@media (max-width: 1199px) {
  .nav__item {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .nav__item {
    margin-right: 0;
    margin-bottom: 15px;
  }
  .nav__item:first-child {
    margin-top: 40px;
  }
}
.nav__item:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

.nav__item a {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  padding: 5px 0;
}
@media (max-width: 1199px) {
  .nav__item a {
    padding: 3px 0;
  }
}
@media (max-width: 767px) {
  .nav__item a {
    font-size: 16px;
    line-height: 19px;
  }
}

.social {
  display: flex;
  flex-wrap: wrap;
  max-width: 97px;
}
@media (max-width: 1199px) {
  .social {
    margin-right: -5px;
  }
}

.social__item {
  margin-right: 11px;
}
.social__item:nth-child(3n) {
  margin-right: 0;
  margin-bottom: 5px;
}

